<template>
	<view>
		<text>首页</text>

		<text>查看每月消费记录，默认显示本月</text>
	</view>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		onLoad() {
			
		},
		onShow() {
			uni.$u.http.get(
				'/user/hello', {
					params: {
						userName: 'name',
						password: '123456'
					},
					/* 会加在url上 */
					header: { 
						token: getApp().globalData.token,
						"Access-Control-Allow-Origin": "*",
						"Origin": "http://localhost:8000"
					},
					/* 会与全局header合并，如有同名属性，局部覆盖全局 */
					dataType: 'json',
				}
			).then(res => {
				console.log(res.data)
			}).catch(err => {
				console.log(err)
			})
		},
		methods: {

		}
	}
</script>

